



















































































































.appList {
    // min-height: 100%;
    overflow-y: scroll;
}
.download {
    border-top: 1px solid var(--border-color);
    background-color: var(--bglight);
    z-index: 99;
    bottom: 0;
    left: 0;
}
.expanded {
    min-height: 100%;
    display: flex;
    flex-direction: column;
}
.expanded > * {
    padding: 10px;
}
.queuetext {
    white-space: nowrap;
}
.packagelist {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.5s;
}
.packagelist-item:not(:last-child)::after {
    content: ",";
}
.sidebar-content {
    background-color: var(--background) !important;
    color: var(--text) !important;
}
.not-expanded {
    // display: none;
    margin: 10px;
    padding: 0 !important;
    gap: 5px;
    height: calc(
        100% - 20px
    ); // a hack i guess? i fucking hate counter strike source
}
.sidebar-content {
    overflow: hidden !important;
}
.tilted-text {
    writing-mode: vertical-rl;
}
.b-sidebar {
    .sidebar-content {
        &.is-mini {
            &:not(.is-mini-expand),
            &.is-mini-expand:not(:hover):not(.is-mini-delayed) {
                .expanded {
                    display: none;
                }
                .not-expanded {
                    display: inline-flex;
                }

                width: 3em !important;
                max-width: 3em !important;
            }
        }
    }
}
.b-sidebar {
    // transition: width 1s;
}
.b-sidebar .sidebar-content {
    width: 40% !important;
    max-width: 40%;
}
